import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { Popover, PopoverContent } from "carbon-components-react";

const dispatchedEvents = ["ABOUT#clicked"];

/*
    Example: <srs-popover status="error" content="Please contact system administrator." prefix="error"/>
*/
const SrsPopover = function (props, ref) {
    const [isOpen, setIsOpen] = useState(false);

    const handlePopoverOpen = () => {
        debugger;
        setIsOpen(true);
    };

    const handlePopoverClose = () => {
        setIsOpen(false);
    };

    return (
        <Popover
            open={isOpen}
            onClose={handlePopoverClose}
            position={props.position}
            align={props.start}>
            <div className="playground-trigger" onClick={handlePopoverOpen}>
                <slot name="trigger"></slot>
            </div>
            <PopoverContent className="p-3">
                <slot name="content" />
            </PopoverContent>

        </Popover>
    );
}

const styles = `
    
`
const props = []

const slots = [{name: "trigger"}, {name: "content"}];

const WrappedComponent = wrapper(React.forwardRef(SrsPopover), props);
export { WrappedComponent, styles, props, dispatchedEvents };